<template>
  <div>
    <b-card
      ref="cardAction"
      :title="$t('pages.rejectedContracts.title')"
    >
      <b-row>
        <b-col cols="12">
          <b-table
            id="my-table"
            :per-page="perPage"
            :current-page="currentPage"
            head-variant
            hover
            responsive
            :items="getContracts"
            :fields="fields"
            bordered
          >
            <template #head()="data">
              <span
                class="text-primary"
                style="font-size:18px"
              >{{ data.label }}</span>
            </template>

            <template #table-busy>
              <div class="text-center text-dark my-2">
                <b-spinner class="align-middle" />
                <strong> Loading... </strong>
              </div>
            </template>

            <template #cell(id)="row">
              {{ row.item.id }}
            </template>

            <template #cell(created_at)="row">
              {{ new Date(row.item.created_at).toDateString() }}
            </template>

            <template #cell(status)="row">
              {{ row.item.status }}
            </template>

            <template #cell(actions)="row">
              <b-button
                class="mr-1"
                size="sm"
                variant="primary"
                @click="showDetails(row.item.id)"
              >
                {{ $t('buttons.details') }}
              </b-button>
              <b-button
                class="mr-1"
                size="sm"
                variant="success"
                @click="confirmAccepted(row.item.id, 'A')"
              > {{ $t('buttons.active') }} </b-button>
              <b-button
                class=""
                size="sm"
                variant="danger"
                @click="confirmDelete(row.item.id)"
              > {{ $t('buttons.delete') }} </b-button>
            </template>
          </b-table>
        </b-col>
      </b-row>
      <div class="mt-3">
        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          align="left"
          :per-page="perPage"
          aria-controls="my-table"
        />
      </div>
    </b-card>
    <company-details :obj="obj" />
  </div>
</template>

<script>
import {
  BRow, BCol, BTable, BButton, BPagination, BCard,
} from 'bootstrap-vue'
import { faEye, faTrash, faPlus } from '@fortawesome/free-solid-svg-icons'
import { library } from '@fortawesome/fontawesome-svg-core'
import CompanyDetails from './companyDetails.vue'

library.add(faEye, faTrash, faPlus)

export default {
  name: 'AllContractsRejected',
  components: {
    BCard,
    BRow,
    BCol,
    BTable,
    BButton,
    BPagination,
    CompanyDetails,
  },
  data() {
    return {
      perPage: 6,
      currentPage: 1,
      add: true,
      obj: {},
      busy: true,
      file: null,
      fields: [
        { key: 'id', label: this.$t('pages.rejectedContracts.id') },
        { key: 'created_at', label: this.$t('pages.rejectedContracts.createdAt') },
        { key: 'is_mosque', label: this.$t('pages.rejectedContracts.isMosque') },
        { key: 'status', label: this.$t('pages.rejectedContracts.status') },
        { key: 'actions', label: this.$t('pages.rejectedContracts.action') },
      ],
    }
  },
  computed: {
    rows() {
      return this.getContracts.length
    },
    getContracts() { return this.$store.state.contract.contracts },
  },
  created() {
    this.$store.dispatch('contract/fetch', 'Rejected')
  },
  methods: {
    confirmAccepted(id, status) {
      this.$swal({
        title: this.$t('pages.newContracts.confirmAccepted.title'),
        text: this.$t('pages.newContracts.confirmAccepted.text'),
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: this.$t('message.accept'),
        cancelButtonText: this.$t('message.cancel'),
        customClass: {
          confirmButton: 'btn btn-success',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          if (status === 'A') {
            const Obj = {
              contract_id: id,
              remove: true,
              status,
            }
            this.$store.dispatch('contract/active', Obj).then(() => {
              this.$swal({
                icon: 'success',
                title: this.$t('pages.newContracts.messageAccepted.title'),
                text: this.$t('pages.newContracts.messageAccepted.text'),
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
            })
          }
        }
      })
    },
    confirmDelete(id) {
      this.$swal({
        title: this.$t('pages.rejectedContracts.confirmDelete.title'),
        text: this.$t('pages.rejectedContracts.confirmDelete.text'),
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: this.$t('message.accept'),
        cancelButtonText: this.$t('message.cancel'),
        customClass: {
          confirmButton: 'btn btn-danger',
          cancelButton: 'btn btn-outline-primary ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          this.$swal({
            icon: 'success',
            title: this.$t('pages.rejectedContracts.messageDelete.title'),
            text: this.$t('pages.rejectedContracts.messageDelete.text'),
            customClass: {
              confirmButton: 'btn btn-success',
            },
          })
          this.$store.dispatch('contract/delete', id).then(() => {
          })
        }
      })
    },
    showDetails(obj) {
      this.obj = obj
      this.$bvModal.show('companyDetails')
    },
  },
}
</script>

<style scoped></style>
